.footer-invisible {
  display: none;
}
.parent-footer {
  flex-grow: 1;
  display: flex;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  background: #ffffff;
}
.parent-footer .footer {
  height: 40px;
  width: 100%;
  background-color: rgba(212, 215, 217, 0.15);
  padding: 0;
  margin: 0;
  align-self: flex-end;
}
.parent-footer .footer .footer-copyrights {
  padding-left: 24px;
}
.parent-footer .footer .footer-copyrights p {
  padding: 0;
  margin: 0;
  width: 148px;
  font-family: "AvenirNext-Regular";
  font-size: 10px;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: normal;
  text-align: center;
  color: #7f888d;
}
