#root .id-proof.fixed_header_sticky {
  align-items: center;
  justify-content: space-between;
}
#root .id-proof.fixed_header_sticky .upload-btn {
  margin: 16px;
  height: 45px;
  border-radius: 4px;
  color: #32b9d1;
}
#root .consent-document-viewer {
  margin: auto;
}
#root .consent-document-viewer .footer {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  height: 72px;
  border-top: solid 1px #d4d7d9;
  z-index: 2;
  background: white;
}
#root .consent-document-viewer .footer .upload-btn {
  margin: 16px;
  height: 45px;
  border-radius: 4px;
  background-color: #32b9d1;
  color: #ffffff;
}
#root .consent-document-viewer .footer.hide-desktop {
  border-top: solid 1px #d4d7d9;
  position: fixed;
  bottom: 0px;
  right: 16px;
  width: 100%;
}
#root .ant-btn.prev[disabled],
#root .ant-btn.next[disabled] {
  color: rgba(0, 0, 0, 0.25);
  background-color: #f5f5f5;
  border: transparent;
}
@media (min-width: 1025px) {
  #root .consent-document-viewer {
    margin-top: 170px;
  }
}
#root .pdf-viewer {
  padding-bottom: 60px;
  width: 100%;
  height: 100%;
}
#root .pdf-viewer .page-number {
  height: 60px;
  margin: 10px 10px 10px 0px;
  align-items: center;
  justify-content: flex-end;
  bottom: 72px;
  display: flex;
  flex-direction: row;
}
#root .pdf-viewer .page-number .text {
  padding: 10px 10px 0px 10px;
}
#root .pdf-viewer .padding-10 {
  padding: 10px;
}
